let mapStateToProps = (state) => {
    return {
        id: state.sideBar.id,
        avatar: state.sideBar.avatar,
        name: state.sideBar.name
    }
}

const FriendsContainer = connect(mapStateToProps)(Friends)

export default FriendsContainer;